import {profileAPI} from "../api/api";

import {stopSubmit} from "redux-form";


const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';

const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';
// const SAVE_PROFILE_SUCCESS = 'SAVE_PROFILE_SUCCESS';


let initialState = {
    post: [
        {id: 1, message: 'Hi, i am first', likesCount: 0},
        {id: 2, message: 'Hi i am two', likesCount: 5},
        {id: 3, message: 'Hi i am three', likesCount: 15}
    ],
    profile: null,
    status: ""
}

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                id: 5,
                message: action.newPostText,
                likesCount: 0
            };
            return {
                ...state,
                post: [...state.post, newPost],
                newPostText: ''
            };
        }

        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            };
        }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }


        case SAVE_PHOTO_SUCCESS:
            return {
                ...state,
                profile: {...state.profile, photos: action.photos}
            }

        // case SAVE_PROFILE_SUCCESS:
        //     return {
        //         ...state,
        //         profile: {...state.profile, profile: action.profile}
        //     }

        case DELETE_POST :
            return {...state, post: state.post.filter(p => p.id !== action.postID)}

        default:
            return state;
    }
}

export const addPostActionCreator = (newPostText) => ({type: ADD_POST, newPostText});

export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});
export const setStatus = (status) => ({type: SET_STATUS, status});
export const deletePost = (postID) => ({type: DELETE_POST, postID});

export const savePhotoSuccess = (photos) => ({type: SAVE_PHOTO_SUCCESS, photos});
// export const saveProfileSuccess = (profile) => ({ type: SAVE_PROFILE_SUCCESS, profile });


export const getUserProfile = (userId) => (dispatch) => {
    profileAPI.getProfile(userId).then(response => {
        dispatch(setUserProfile(response.data));
    });
}

export const getStatus = (userId) => (dispatch) => {
    profileAPI.getStatus(userId).then(response => {
        dispatch(setStatus(response.data));
    });
}

export const updateStatus = (status) => (dispatch) => {
    profileAPI.updateStatus(status).then(response => {

        try {
            if (response.data.resultCode === 0) {
                dispatch(setStatus(status));
            }
        } catch (error) {
            alert(error)
        }
    });
}

export const savePhoto = (file) => async (dispatch) => {
    let response = await profileAPI.savePhoto(file)

    if (response.data.resultCode === 0) {
        dispatch(savePhotoSuccess(response.data.data.photos));
    }
}

export const saveProfile = (profile) => async (dispatch, getState) => {

    const userId = getState().auth.userId;
    let response = await profileAPI.saveProfile(profile)

    if (response.data.resultCode === 0) {
        dispatch(getUserProfile(userId));
    } else {
        dispatch(stopSubmit("profile", {_error: response.data.messages[0]}));
        return Promise.reject(response.data.messages[0]);
    }
}

export default profileReducer;
