import React from "react";
import {Field, reduxForm} from "redux-form";

import {createField, Input} from "../common/formcontrols/FormsControls";

import {required} from "../../utils/validators/validators";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {Redirect} from "react-router-dom";
import style from "./../common/formcontrols/FormsControls.module.css"


const LoginForm =({handleSubmit, error, captchaUrl}) => {
    return <div>
        <h1>LOGIN</h1>
        <form onSubmit={handleSubmit}>

            <div>
                <Field component={Input} validate={[required]} name={"email"} type="text" placeholder={"Email"}/>
            </div>
            <div>
                <Field component={Input} validate={[required]} name={"password"} type="password" placeholder={"Password"}/>
            </div>
            <div>
                <Field component={Input} name={"rememberMe"} type="checkbox"/> remember me
            </div>


            { captchaUrl && <img src={captchaUrl} alt={'img'}/>}
            { captchaUrl && createField("Symbols from image", "captcha", [],Input,{})}

            {error &&
                <div className={style.formSummaryError}>
                    {error}

                </div>
            }
            <div>
                <button>Login</button>
            </div>
        </form>
    </div>
}

const LoginReduxForm = reduxForm({
    form: "login"
})(LoginForm)

const Login =(props) => {
    const onSubmit = (formData) => {

      props.login(formData.email, formData.password, formData.rememberMe, formData.captcha)

    }
    if (props.isAuth){
        return <Redirect to={'/profile'}/>
    }
    return <div>

        <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl}/>

    </div>
}

const mapStateToProps = (state) => ({

    isAuth: state.auth.isAuth,
    captchaUrl: state.auth.captchaUrl

})

export default connect(mapStateToProps, {login})(Login);